export default {
  name: 'NavSub',
  methods: {
        handleScroll: function (event) {
          const scrollPos = $(window).scrollTop();
          if (scrollPos > 10) {
            $("#nav").addClass("navihi");
            $("#nav a").addClass("transparentwhite");
            $("#nav a:hover").addClass("transparentwhite");
            $("#nav a.router-link-exact-active").addClass("transparentwhite");
            $("img#logoBlack").addClass("small");
            $("#line1").removeClass("noline");
            $("#line1").addClass("line");
          } else {
            $("#nav").removeClass("navihi");
            $("#nav a").removeClass("transparentwhite");
            $("#nav a:hover").removeClass("transparentwhite");
            $("#nav a.router-link-exact-active").removeClass("transparentwhite");
            $("img#logoBlack").removeClass("small");
            $("#line1").removeClass("line");
            $("#line").addClass("noline");
          }
        }
    },

    created: function () {
        window.addEventListener('scroll', this.handleScroll);
    },
    destroyed: function () {
        window.removeEventListener('scroll', this.handleScroll);
    }
}



